`<!--大黄蜂机器人通讯结构-->
<template>
    <h3>大黄蜂机器人通讯结构</h3>
    <p>连接方式         : 串口</p>
    <p>配置参数baudRate : 115200</p>
    <p>配置参数parity   : 0</p>
    <p>配置参数dataBits : 8</p>
    <p>配置参数stopBits : 1</p>
    <el-button type="primary" v-on:click="requestSerialPort()">连接</el-button>
    <label> 连接成功后，发送[0xFF, 0xFF, 0x02, 0xF5, 0xF7] 指令0xF5是进入PC编程模式 0xF7是(指令+数据)和%256的结果
         <br> 发送后接收数据直到接收[0xFF,0xFF,0x04	0xF4,0x4F 0x4B,0x93] 当中的数据需要进行处理</label>
    <p></p>
    <input type="file" @change="actionDownload"/>
    <p></p>
    <label>下载是将pc上的数据载入到机器人当中，这里用一个.shr机器人保存再本地的文件开始,下载的指令顺序是:删除(0x07),创建(0X06),下载(0x02)</label>
    <p></p>
</template> 
<script>


import    { CreatePosData } from "/src/PosData.js";
import iconv from 'iconv-lite';

    export default{
        data(){
            return{
                port:{},//通讯接口
                isLink:false,//是否连接
                initData:[],//存放4个初始位置
                dataArr: [],
            }
        },
        methods:{
            async requestSerialPort()//连接
            {
                try
                {
                    const port = await navigator.serial.requestPort();
                    if(port!=null)
                    {
                        await port.open({baudRate:115200,parity:"none",dataBits:8,stopBits:1})
                        this.port = port;
                        const data = new Uint8Array([0xFF,0xFF,0X02,0xF5,0xF7]);
                        const writer =  port.writable.getWriter();
                        await writer.write(data);
                        await writer.releaseLock();
                        //this.isLink= true;
                        const reader = await port.readable.getReader();
                        this.isLink= false;
                        var dataValue = []; 
                       
                        while(true)
                        {
                            if(this.isLink == true)
                            {
                                
                                reader.releaseLock();
                                break;
                            }
                            const{value,done} = await reader.read();
                            if(done)
                            {
                                reader.releaseLock();
                                break;
                            }
                            for(let i=0; i< value.length;i++)
                            {
                                dataValue[dataValue.length] = value[i]
                            }
                            for(let i=0; i<dataValue.length;i++)
                            {
                                //帧头和指令判断
                                if(dataValue[i] == 0xFF && dataValue[i+1]==0xFF)
                                {
                                     var len = dataValue[i+2];//长度
                                     var order = dataValue[i+3];//指令
                                     if(len == 0x04 && order ==245 && dataValue[i+4] == 0x4F && dataValue[i+5]==0x4B )
                                     {
                                        if(dataValue[i+6] == (len+order +0x4F+0x4B )% 256)
                                        {
                                            this.isLink =true;
                                            break;
                                        }
                                     }
                                }
                            }
                        }
                        let  flag = 0;
                        for(let i=0; i<dataValue.length;i++)
                        {
                            if(dataValue[i] == 0xFF && dataValue[i+1]== 0xFF)
                            {
                                var len    =  dataValue[i+2];//长度
                                var order  =  dataValue[i+3];//指令
                                if(len == 50 && order == 245)
                                {
                                    var dataArray = [];
                                    for(let j=0; j<48; j++)
                                    {
                                        dataArray[j] = dataValue[i + j + 4];
                                    }
                                    i+=48 + 1 + 3;
                                    this.initData[flag] = dataArray;
                                    flag++;
                                }
                            }
                        }
                        console.log(this.initData);

                    }
                }
                catch(err)
                {
                    console.error(err);
                }
            },
            async actionDownload(event)
            {
                try
                {
                    const file = event.target.files[0]
                    const reader = new FileReader()
                    reader.readAsText(file, "UTF-8")
                    reader.onload = async (evt) =>
                    {
                       const fileString = evt.target.result
                     
                       const count = fileString.trim().split('\n').length
                        this.dataArr = [];
                        for(let index = 0; index < count ; index++)
                        {
                            const fileline = fileString.split("\r\n")[index].split(",")[0]
                            const filelineNumber = (fileline.split(/\s+/)).map(match => isNaN(match) ? match : match==""?" ": Number(match));
                            this.dataArr.push(filelineNumber)
                        }
                        if(this.isLink == true)
                        {
                            var nameArr = [];
                            var dataAction = [];
                            var dataArr  = this.dataArr;
                            var len = dataArr.length;
                            for(let i=0; i<len;i++)
                            {
                                var _is_Action =  false;
                                if(dataArr[i][0]==' ')
                                {
                                    _is_Action = true;
                                }
                                if(dataArr[i][0] == 253 && dataArr[i][1]==253 && dataArr[i][2] == 254 && dataArr[i][3]!=" ")
                                {
                                    nameArr.push(dataArr[i][3]);
                                }
                                if(_is_Action == true)
                                {
                                    dataAction.push(dataArr[i]);
                                }
                            }
                            var last_loop_byte = 0;
                            len = dataAction.length;
                            var  posDataArr =[];
                            for(let i=0; i<len; i++)
                            {
                                var _n1 = dataAction[i][5];
                                var _n2 = dataAction[i][6];
                                var _n3 = dataAction[i][7];
                                var _n4 = dataAction[i][8];
                                var _n5 = dataAction[i][9];
                                var _n6 = dataAction[i][10];
                                for(let j = 0; j<8; j++ )
                                {
                                    let _Zero = _n1 & Math.pow(2,7-j);
                                    if(_Zero != 0)
                                    {
                                        dataAction[i][j+11] *=-1;
                                    }
                                }
                                for(let j = 0; j< 8; j++)
                                {
                                    let _Zero = _n2 & Math.pow(2,7-j);
                                    if(_Zero != 0)
                                    {
                                        dataAction[i][j+19] *= -1;
                                    }
                                }
                                for(let j = 0; j<8 ; j++)
                                {
                                    let _Zero = _n3 & Math.pow(2,7-j);
                                    if(_Zero != 0)
                                    {
                                        dataAction[i][j+27] *= -1;
                                    }
                                }
                                for(let j=0; j<8 ; j++)
                                {
                                    let _Zero = _n4 & Math.pow(2,7-j);
                                    if(_Zero!=0)
                                    {
                                        dataAction[i][j+35] *= -1;
                                    }
                                }
                                for(let j=0;j<8;j++)
                                {
                                    let _Zero = _n5 & Math.pow(2,7-j);
                                    if(_Zero != 0)
                                    {
                                        dataAction[i][j+43] *=-1;
                                    }
                                }
                                for(let j=0;j<8 ;j++)
                                {
                                    let _Zero = _n6 & Math.pow(2,7-j);
                                    if(_Zero != 0 )
                                    {
                                        dataAction[i][51]*=-1;
                                    }
                                }
                                var Loop_Time =0;
                                var Is_Loop_Start  = false;
                                var Is_Loop_End = false;
                                if(dataAction[i][4] != 0 && dataAction[i][4]>128)
                                {
                                    Loop_Time = dataAction[i][4] -128;
                                }
                                else if(dataAction[i][4]!= 0 && dataAction[i][4]>128)
                                {
                                    Loop_Time =dataAction[i][4];
                                    Is_Loop_Start =false;
                                    Is_Loop_End  =true;
                                }
                                else if(dataAction[i][4] == 0)
                                {
                                    Is_Loop_Start =false;
                                    Is_Loop_End =false;
                                }
                                var Pos = [];
                                var number =dataAction[i][3];
                                for(let j = 0 ; j<48 ;j++)
                                {
                                    Pos[j] = dataAction[i][j+11];
                                }
                                var Speed = dataAction[i][59];
                                var Delay = dataAction[i][60];
                                var Music_State = 0;
                                var Music_Number = -1;
                                var Music_Chosen = -1;
                                if((dataAction[i][61]!=251 && dataAction[i][62]!=251 ) &&(dataAction[i][61]!=252 && dataAction[i][62]!=252 ))
                                {
                                    Music_Chosen = dataAction[i][61] * 256 + dataAction[i][62];
                                    Music_State = 2;
                                } 
                                else if(dataAction[i][61] == 251 && dataAction[i][62]==251)
                                {
                                    Music_State = 4;
                                }
                                else if(dataAction[i][61] == 252 && dataAction[i][62] ==252)
                                {
                                    Music_State=3;
                                }
                                Music_Number = Music_Chosen;
                                if(dataAction[i][4] != 0)
                                {
                                    if(last_loop_byte == 0)
                                    {
                                        Is_Loop_Start =true;
                                    }
                                    else
                                    {
                                        Is_Loop_Start =false;
                                    }
                                    if(dataAction[i][4]<128)
                                    {
                                        Is_Loop_End=true;
                                        Loop_Time= dataAction[i][4];
                                    }
                                    else 
                                    {
                                        Is_Loop_End=false;
                                        Loop_Time= dataAction[i][4]-128;
                                    }
                                }
                                if(dataAction[i][4]>128)
                                {
                                    last_loop_byte = dataAction[i][4];
                                }
                                else
                                {
                                    last_loop_byte=0;
                                }
                                var posData = 
                                {
                                    number : number ,
                                    Name :1,
                                    Pos : Pos,
                                    Speed :Speed,
                                    Delay :Delay,
                                    Loop_Time:Loop_Time,
                                    Is_Loop_Start:Is_Loop_Start,
                                    Is_Loop_End:Is_Loop_End,
                                    Music_State:Music_State,
                                    Music_Number:Music_Number
                                }
                                posDataArr.push(CreatePosData(posData));
                            }

                            len = posDataArr.length;
                            let currentnumber = 0;
                            var Loop_Time = -1;
                            var Is_In_Loop = false;
                            var currentData = [[]];
                            var flagNumber = posDataArr[0].number;
                            currentData[0] =[]
                            for(let i=0;i<len ;i++)
                            {
                                if(flagNumber!= posDataArr[i].number)
                                {
                                    flagNumber = posDataArr[i].number;
                                    currentnumber++; 
                                    currentData[currentnumber] =[]
                                }
                                var currentPos = [];
                                currentPos.push(0xFF);
                                currentPos.push(0xFF);
                                currentPos.push(0x3D);
                                currentPos.push(0x02);
                                if(!Is_In_Loop)
                                {
                                    currentPos[4] = 0
                                }
                                if(Is_In_Loop && ! posDataArr[i].Is_Loop_Start && !posDataArr[i].Is_Loop_End)
                                {
                                    currentPos[4] = Loop_Time + 128;
                                }
                                if(posDataArr[i].Is_Loop_End)
                                {
                                    Is_In_Loop=false;
                                    currentPos[4] = Loop_Time;
                                    Loop_Time =0;
                                }
                                if(posDataArr[i].Is_Loop_Start)
                                {
                                    Is_In_Loop = true;
                                    Loop_Time = posDataArr[i].Loop_Time;
                                    currentPos[4] = Loop_Time+128;
                                }
                                var _int_temp1 = 0; 
                                var _int_temp2 = 0; 
                                var _int_temp3 = 0;
                                var _int_temp4 = 0; 
                                var _int_temp5 = 0; 
                                var _int_temp6 = 0;

                                for(let k=0; k<48;k++)
                                {
                                    if(k<8)
                                    {
                                        if(posDataArr[i].Pos[k]<0)
                                        {
                                            _int_temp1 += parseInt(Math.pow(2,7-k));
                                            posDataArr[i].Pos[k] = posDataArr[i].Pos[k] * -1;
                                        }
                                    }
                                    if(k>=8 && k<16)
                                    {
                                        if(posDataArr[i].Pos[k] <0)
                                        {
                                            _int_temp2 += parseInt(Math.pow(2,15-k))
                                            posDataArr[i].Pos[k] = posDataArr[i].pos[k] * -1;
                                        }
                                    }
                                    
                                    if(k>=16 && k<24)
                                    {
                                        if(posDataArr[i].Pos[k]<0)
                                        {
                                            _int_temp3+= parseInt(Math.pow(2,23-k));
                                            posDataArr[i].Pos[k]= posDataArr[i].Pos[k] * -1;
                                        }
                                    }
                                    if(k>=24 && k<32)
                                    {
                                        if(posDataArr[i].Pos[k]<0)
                                        {
                                            _int_temp4+= parseInt(Math.pow(2,31-k));
                                            posDataArr[i].Pos[k]= posDataArr[i].Pos[k] * -1;
                                        }
                                    }
                                    if(k>=32 && k<40)
                                    {
                                        if(posDataArr[i].Pos[k]<0)
                                        {
                                            _int_temp5+= parseInt(Math.pow(2,39-k));
                                            posDataArr[i].Pos[k]= posDataArr[i].Pos[k] * -1;
                                        }
                                    }
                                    if(k>=40 && k<48)
                                    {
                                        if(posDataArr[i].Pos[k]<0)
                                        {
                                            _int_temp6+= parseInt(Math.pow(2,47-k));
                                            posDataArr[i].Pos[k]= posDataArr[i].Pos[k] * -1;
                                        }
                                    }
                                }
                                currentPos[5] = _int_temp1;
                                currentPos[6] = _int_temp2;
                                currentPos[7] = _int_temp3;
                                currentPos[8] = _int_temp4;
                                currentPos[9] = _int_temp5;
                                currentPos[10] = _int_temp6;
                                for(let k=0; k<48;k++)
                                {
                                    currentPos[k+11]= posDataArr[i].Pos[k]
                                }

                                currentPos[59] = posDataArr[i].Speed;
                                currentPos[60] = posDataArr[i].Delay;
                                switch(posDataArr[i].Music_State)
                                {
                                    case 1:
                                        currentPos[61] = parseInt(posDataArr[i].Music_Number / 256);
                                        currentPos[62] = parseInt(posDataArr[i].Music_Number % 256);
                                     break;
                                    case 2:
                                        currentPos[61] = parseInt(posDataArr[i].Music_Number / 256);
                                        currentPos[62] = parseInt(posDataArr[i].Music_Number % 256);
                                    break;
                                    case 3:
                                        currentPos[61] = 252;
                                        currentPos[62] = 252;
                                    break;
                                    case 4:
                                        currentPos[61] = 251;
                                        currentPos[62] = 251;
                                     break;
                                }
                                var sum =0; 
                                for(let k=2; k<63;k++)
                                {
                                    sum += currentPos[k];
                                }
                                sum = sum % 256;
                                currentPos[63] = sum; 
                                currentData[currentnumber].push(currentPos)
                            }
                            len = nameArr.length;
                            console.log(currentData);
                            for(let i=0; i<len ;i++)
                            {
                                let promise = 
                                new Promise((resolve,reject)=>{
                                    setTimeout(() => resolve(), 50)
                                });
                                await  promise;
                                var selectedName = nameArr[i];
                                var nameByte = Array.from(iconv.encode(selectedName,'utf8'));
                                var length = nameByte.length;
                                var senLength = length + 2;
                                var createCMD = [];
                                var sum=0;
                                createCMD[0] = 0xFF;
                                createCMD[1] = 0xFF;
                                createCMD[2] = senLength;
                                createCMD[3] = 0x07;//删除
                                sum = senLength + 0x07;
                                for(let j= 0; j<length ;j++)
                                {
                                    createCMD.push(nameByte[j]);
                                    sum = sum +nameByte[j];
                                }
                                sum =sum % 256;
                                createCMD.push(sum);
                                var dataut = new Uint8Array(createCMD);
                                const writer = this.port.writable.getWriter();
                                await writer.write(dataut);
                                const reader = await this.port.readable.getReader();
                                var dataValue = [];
                                var redResult  = false;
                              
                                // while(!redResult)
                                // {
                                //     const{value,done} = await reader.read();
                                //     if(done)
                                //     {
                                //         reader.releaseLock();
                                //         break;
                                //     }  
                                //     for(let i = 0 ; i< value.length;i++)
                                //     {
                                //         dataValue[dataValue.length] =value[i];
                                //     }
                                //     console.log(dataValue);
                                //     for(let i = 0 ; i< dataValue.length;i++)
                                //     {
                                //         if(dataValue[i] == 0xFF && dataValue[i+1] ==0xFF)
                                //         { 
                                //             var order = dataValue[i + 3];//指令
                                //             if (order== 0x07 && dataValue[i + 4] == 0x4f && dataValue[i + 5] == 0x4B)
                                //             {
                                               
                                //                 redResult=true;
                                //             }
                                //         }

                                //      }
                        
                                // }
                               
                                let promise1 = new Promise((resolve, reject) => {
                                   setTimeout(() => resolve(), 50)
                                    });
                                await  promise1;
                                createCMD[3] = 0x06;//创建指令
                                createCMD[createCMD.length-1] =  createCMD[createCMD.length-1]-1;
                                dataut = new Uint8Array(createCMD);
                                await writer.write(dataut);
                                dataValue =[];    
                                redResult =false;

                                let promise2 = new Promise((resolve, reject) => {
                                   setTimeout(() => resolve(), 50)
                                    });
                                await  promise2;
                               
                                //上传数据
                                for(let j = 0 ; j< currentData[i].length;j++)
                                 {
                                    let promises = new Promise((resolve, reject) => {
                                                    setTimeout(() => resolve(), 150)
                                                         });
                                    await  promises;
                                    var  dataSen = new Uint8Array(currentData[i][j]);
                                    console.log(dataSen);
                                    await writer.write(dataSen);
                                    dataValue =[];    
                                    redResult =false;
                               
                                    // while(!redResult)
                                    // {
                                    //     const{value,done}  = await reader.read();
                                    //     if(done)
                                    //     {
                                    //         reader.releaseLock();
                                    //         break;
                                    //     }  
                                    //     for(let i = 0 ; i< value.length;i++)
                                    //     {
                                    //         dataValue[dataValue.length] =value[i];
                                    //     }
                                    //     for(let i = 0 ; i< dataValue.length;i++)
                                    //     {
                                    //         if(dataValue[i] == 0xFF && dataValue[i+1] ==0xFF)
                                    //         { 
                                    //             var order = dataValue[i + 3];//指令
                                    //             if (order== 0x02 && dataValue[i + 4] == 0x4f && dataValue[i + 5] == 0x4B)
                                    //             {
                                    //                  console.log(i+" "+dataValue);
                                    //                  redResult=true;
                                    //             }
                                    //         }
                                    //     }
                                    // }
                        
                                 }
                                  await writer.releaseLock();
                                  await reader.releaseLock();
                                

                            }
                            console.log("上传完成");
                        }

                    };
                }
                catch(err)
                {

                }
            }
        }
    }
</script>`