//用于上传下载的数据处理类
export default class PosData{
    constructor({number,Name,Pos,Speed,Delay,Loop_Time,Is_Loop_Start,Is_Loop_End,Music_State,Music_Number})
    {
        this.number = number,//点位编号，对应的是那个动作下的点
        this.Name = Name,//无作用
        this.Pos = Pos,//点位信息的集合
        this.Speed = Speed,//速度
        this.Delay = Delay,//延迟
        this.Loop_Time = Loop_Time,//循环次数
        this.Is_Loop_Start = Is_Loop_Start,//是否是循环的开始
        this.Is_Loop_End = Is_Loop_End,//是否是循环的结束
        this.Music_State=Music_State,//0代表未选择，1代表指导音乐，2代表自定义音乐，3代表继续，4代表停止
        this.Music_Number= Music_Number//音乐的文件夹以及选择的那个音乐 那个文件夹是这个数/256 那个文件是这个数%256
    }   
}

export function CreatePosData(posDataInit){
    //console.log(posDataInit)
    return new PosData({
        number : posDataInit.number,
        Name : posDataInit.Name,
        Pos : posDataInit.Pos,
        Speed : posDataInit.Speed,
        Delay : posDataInit.Delay,
        Loop_Time:posDataInit.Loop_Time,
        Is_Loop_Start : posDataInit.Is_Loop_Start,
        Is_Loop_End : posDataInit.Is_Loop_End,
        Music_State:posDataInit.Music_State,
        Music_Number:posDataInit.Music_Number
    })
}


