import Vue from "vue";
import { createRouter, createWebHashHistory } from "vue-router";

import HomePage from "@/components/Home.vue";
import Convoy_Seven from "@/components/Convoy_Seven.vue";
import Bumblebee from "@/components/Bumblebee.vue";
import SteelCable from "@/components/SteelCable.vue";

const router = createRouter({
  mode: "hash",
  history: createWebHashHistory(),
  routes: [
    {
      path: "/",
      name: "HomePage",
      component: HomePage,
    },
    {
      path: "/Convoy_Seven",
      name: "Convoy_Seven",
      component: Convoy_Seven,
    },
    {
      path: "/Bumblebee",
      name: "Bumblebee",
      component: Bumblebee,
    },
    {
      path: "/SteelCable",
      name: "SteelCable",
      component: SteelCable,
    },
  ],
});

export default router;
